@import url(https://fonts.googleapis.com/css?family=Montserrat:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Merriweather:400,400i,700&display=swap);
*{
  margin: 0;
  padding: 0;
}

body{
  margin: 0;
  padding: 0;
  font-family: "neosans regular", "Helvetica Neue", Helvetica, Arial, Sans-serif;
  line-height: 1.4;
  background-color: #282828;
}

a{
  color: #333;
  text-decoration: none;
}

.app {
  max-width: 1000px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  box-sizing: border-box;
  padding: 20px;
  flex-direction: column;
}

@media only screen and (min-width: 1200px) {
  .app{
    padding: 0px;
  }
}
.header {
  width: 100%;
  box-sizing: border-box;
}

.header-title {
  color: #fff;
  font-size: 18px;
  display: flex;
  min-height: 60px;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
}

.header-division{
  width: 30%;
  height: 5px;
  background-color: #d02035;
}

@media only screen and (min-width: 380px) {
  .header-title{font-size: 22px;}
}
@media only screen and (min-width: 600px) {
  .header-title{
    font-size: 35px;
    height: 100px;
  }
  .header-division{height: 7px;}
}
@media only screen and (min-width: 768px) {
  .header-title{
    font-size: 50px;
    height: 120px;
  }
}
@media only screen and (min-width: 992px) {
  .header-title{
    font-size: 65px;
    height: 150px;
  }
  .header-division{height: 10px;}
}
.paragraph {
  color: #fff;
  font-size: 12px;
  margin-top: 25px;
  display: block;
  text-align: justify;
  font-family: 'Merriweather', serif;
}

.signup-form {
  margin-top: 40px;
  font-family: 'Merriweather', serif;
}

.form_error {
  display: block;
  height: 10px;
  text-align: right;
  width: 100%;
  color: #fff;
  font-size: 8px;
}

.form_email {
  width: 100%;
  margin-top: 5px;
  padding: 0 10px;
  box-sizing: border-box;
  outline:none;
}
.form_email.invalid{
  border: 2px solid #d02035;
}

.form_interest {
  width: 100%;
  margin-top: 20px;
  box-sizing: border-box;
  color: #797979;
  padding: 0 10px;
}

.form_input{
  font-size: 12px;
  font-style: italic;
  height: 40px;
}

.form_submit {
  margin-top: 40px;
  width: 100%;
  height: 40px;
  background-color: #d02035;
  outline: none;
  border: none;
  font-weight: bold;
  color: #fff;
  font-size: 13px;
  border-radius: 5px;
}

@media only screen and (min-width: 380px) {
  .paragraph {font-size: 14px;}
  .form_input{font-size: 14px;}
  .form_submit{font-size: 16px;}
  .form_error{font-size: 10px;}
}
@media only screen and (min-width: 600px) {
  .paragraph {font-size: 17px;}
  .form_input{
    font-size: 16px;
    height: 60px;
  }
  .form_submit{
    font-size: 20px;
    height: 60px;
  }
  .form_error{
    font-size: 11px;
    height: 20px;
  }
}
@media only screen and (min-width: 768px) {
  .paragraph {font-size: 20px;}
  .form_data{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
  }
  .form_email-container {
    width: 49%;
    margin-right:1%;
    display: inline-flex;
    flex-direction: column;
  }
  .form_interest {
    width: 49%;
    margin-left: 1%;
    display: inline-flex;
  }
}
.thanks_title {
  color: #fff;
  font-size: 18px;
  margin-top: 25px;
  display: block;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
}

.thanks_message {
  color: #fff;
  font-size: 12px;
  margin-top: 15px;
  display: block;
  text-align: justify;
  font-family: 'Merriweather', serif;
}

@media only screen and (min-width: 380px) {
  .thanks_title{font-size: 20px;}
  .thanks_message{font-size: 14px;}
}
@media only screen and (min-width: 600px) {
  .thanks_title{
    font-size: 35px;
    margin-top: 35px;
  }
  .thanks_message{
    font-size: 20px;
    margin-top: 35px;
  }
}
